import { h, Component } from 'preact';
import 'store/bolt/cookies';
import 'store/bolt/externalLinkModal';
import { joinClasses } from 'utils/utils';
import Store from 'store/store';
import Button from 'components/bolt/button/button';
import Link from 'components/bolt/link/link';
import HTML from 'components/core/html/html';
import ExitPage from 'components/bolt/icons/exitPage';

import s from 'components/bolt/externalLinkModal/externalLinkModal.sss';
import sButton from 'components/bolt/button/button.sss';

export default class ExternalLinkModal extends Component {
	constructor(props) {
		super(props);
		this.clickInterceptor = this.clickInterceptor.bind(this);
		this.closeModal = this.closeModal.bind(this);
		this.onClick = this.onClick.bind(this);
	}

	componentDidMount() {
		document.addEventListener('click', this.clickInterceptor, true);
	}
	componentWillUnmount() {
		document.removeEventListener('click', this.clickInterceptor, true);
	}

	clickInterceptor(e) {
		if (!e?.target || this.props.externalLinkModal?.open) {
			return;
		}
		let link = e.target.closest('a') || e.target.closest('area');
		let linkHref = link?.href;
		if (linkHref?.endsWith('/')) {
			linkHref = linkHref.slice(0, -1);
		}

		if (!linkHref || link.origin === location.origin) {
			return;
		}
		let settings = this.props.appData?.options?.externalLinkModal;
		let urlsWanted = settings?.onlyForUrls?.split('\n').map(str => {
			let url = str.trim();
			if (url.endsWith('/')) {
				url = url.substring(0, url.length - 1);
			}
			return url;
		});
		if (urlsWanted && !urlsWanted.includes(linkHref)) {
			return;
		}
		Store.emit('DISPLAY_EXTERNAL_LINK_MODAL', {
			originalEvent: e,
			onClick: link.onClick,
			target: link.target,
			href: link.href
		});
		e.stopPropagation();
		e.preventDefault();
	}

	onClick(evt) {
		let { onClick, originalEvent } = this.props.externalLinkModal || {};

		this.closeModal();
		if (onClick && typeof onClick === 'function') {
			return onClick(originalEvent || evt);
		}
	}

	closeModal() {
		Store.emit('HIDE_EXTERNAL_LINK_MODAL');
	}

	render(props) {
		const externalLinkModal = props.externalLinkModal;
		if (!externalLinkModal?.open) {
			return <div />;
		}

		let {
			$EXTERNAL_LINK_MODAL_BODY = 'You are leaving this website',
			$EXTERNAL_LINK_MODAL_OK = 'OK',
			$EXTERNAL_LINK_MODAL_CANCEL = 'Cancel'
		} = props.copy.baseCopy;

		return (
			<div class={s.overlay} onClick={this.closeModal}>
				<div class={s.container} onClick={e => e.stopPropagation()}>
					<ExitPage class={s.icon} />
					<HTML class={s.content} content={$EXTERNAL_LINK_MODAL_BODY} markdown />

					<div class={s.buttons}>
						<Button class={s.button} type="outlined" trigger={this.closeModal}>{$EXTERNAL_LINK_MODAL_CANCEL}</Button>
						<Link
							class={joinClasses(
								sButton.container,
								sButton.primary,
								s.button
							)}
							href={externalLinkModal.href}
							ignoreExternalLinkModal
							target={externalLinkModal.target}
							onClick={this.onClick}
						>
							{$EXTERNAL_LINK_MODAL_OK}
						</Link>
					</div>
				</div>
			</div>
		);
	}
}
